import { useState, useEffect } from 'react';
import styled from 'styled-components';
import MailchimpSubscribe from 'react-mailchimp-subscribe';

import { CircularProgress } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

import {
  MAILCHIMP_SIGNUP_API,
  ZETA_GITBOOK_URL,
  ZETA_DISCORD_URL,
} from '../../constants/appConstants';
import ModalGraphicSVG from '../../assets/modal-icon.svg';

const ENTER_KEYCODE = 13;

const CustomForm = ({ status, message, onValidated }: any) => {
  const [email, setEmail] = useState<string>('');
  const [subscribeStatus, setSubscribeStatus] = useState<string>('');

  const handleSubmit = (e: any) => {
    email &&
      email.indexOf('@') > -1 &&
      onValidated({
        EMAIL: email,
      });
    setEmail('');
  };

  useEffect(() => {
    setSubscribeStatus(status);
  }, [status]);

  const handleEnterKeyPress = (e: any) => {
    if (e.keyCode == ENTER_KEYCODE) {
      handleSubmit(e);
    }
  };

  const handleCloseModal = (e: any) => {
    if (e.target.children.length == 1) {
      setSubscribeStatus('');
    }
  };

  function createMarkup() {
    return { __html: message };
  }

  return (
    <>
      {subscribeStatus ? (
        <ModalBackground onClick={handleCloseModal}>
          <Modal>
            <CloseModalContainer>
              <CloseIconWrapper>
                <CloseIcon />
              </CloseIconWrapper>
            </CloseModalContainer>
            <ModalContent>
              {subscribeStatus == 'sending' ? (
                <ProgressContainer>
                  <CircularProgress />
                </ProgressContainer>
              ) : subscribeStatus == 'success' ? (
                <SuccessContainer>
                  <ModalText>
                    <DefiText>Welcome to the ZetArmy</DefiText>
                    <SubText>
                      Thanks for subscribing to our mailing list!
                    </SubText>
                  </ModalText>
                  <CommunityContainer>
                    <CoummunitySubText>
                      Join the Conversation →
                    </CoummunitySubText>
                    <DiscordButtonAnchor href={ZETA_DISCORD_URL}>
                      <DiscordButton>Discord</DiscordButton>
                    </DiscordButtonAnchor>
                  </CommunityContainer>
                </SuccessContainer>
              ) : (
                // May have to refactor to prevent exposing to cross site scripting
                <ModalText>
                  <DefiText>Uh oh...</DefiText>
                  <MailChimpText dangerouslySetInnerHTML={createMarkup()} />
                </ModalText>
              )}
            </ModalContent>
          </Modal>
        </ModalBackground>
      ) : (
        <></>
      )}
      <SplitColumnContainer>
        <LearnMoreAnchor href={ZETA_GITBOOK_URL}>
          <LearnMoreButton>
            <p>Learn More</p>
          </LearnMoreButton>
        </LearnMoreAnchor>
        <SubscribeContainer>
          <SubscribeInput
            id='sign-up'
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder='Stay in the Know'
            autoComplete='off'
            onKeyDown={handleEnterKeyPress}
          />
          <SubscribeButtonContainer>
            {email ? (
              <SubscribeButtonGlow onClick={handleSubmit}>
                Subscribe
              </SubscribeButtonGlow>
            ) : (
              <SubscribeButton>Subscribe</SubscribeButton>
            )}
          </SubscribeButtonContainer>
        </SubscribeContainer>
      </SplitColumnContainer>
    </>
  );
};

export const InputButtonContainer = () => {
  return (
    <MailchimpSubscribe
      url={MAILCHIMP_SIGNUP_API}
      render={({ subscribe, status, message }) => (
        <CustomForm
          status={status}
          message={message}
          onValidated={(formData: any) => subscribe(formData)}
        />
      )}
    />
  );
};

